<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>设备信息管理</div>
            <div class="buttonbox" v-show="!inforadmin&&!equipmentDeployAdmin&&!loginequipmentDeployAdmin">
                <div class="col-sm-1" v-if="$api.opreate.hasLoginComponents(this,'sys:userMan:userOpera')">
                    <select class="form-control" v-model="tableSelectData.pageloginData.state.statetype" @change="changeAudit">
                            <option value="00">全部</option>
                            <option value="01">已部署</option>
                            <option value="10">未审核</option>
                            <option value="11">已审核</option>
                            <option value="20">正在运行</option>
                            <option value="21">已停止</option>
                        </select>
                </div>
                <button type="button" class="btn btn-primary" @click="AduitTrue(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:page:add')&&tableSelectData.pageloginData.state.statetype=='10'">审核</button>
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:page:add')">新增设备</button>
                <button type="button" class="btn btn-info" @click="equipmentDeployClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:page:add')&&(tableSelectData.pageloginData.state.statetype_id=='0'||tableSelectData.pageloginData.state.statetype=='11')">设备部署</button>
                <button type="button" class="btn btn-info" @click="loginEquipmentDeployInfor" v-if="$api.opreate.hasLoginComponents(this,'sys:page:modify')&&tableSelectData.pageloginData.state.statetype=='01'">查看部署详情</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:modify')">修改设备信息</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:delete')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:page:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <div v-show="inforadmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <!-- 设备名 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.equipment_name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入设备名,最多为30位'" v-model="addData.equipment_name" :data-vv-as="'设备名'" v-validate="$api.opreate.validateAdd('name',30,stateid,opreate,'equipment')">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <!-- 设备类型 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备类型</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.equipment_type_id"><option :value="item.equipmentTypeId" v-for="(item,index) of equipment_type" :key="index" v-text="item.equipmentTypeName"></option> </select>
                        </div>
                    </div>
                    <!-- 设备控制类型 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备控制类型</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.control_type_id"><option :value="item.controlTypeId" v-for="(item,index) of control_type" :key="index" v-text="item.controlTypeName"></option> </select>
                        </div>
                    </div>
                    <!-- 设备厂商 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备厂商</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.manufacturer_id"><option :value="item.manufacturerId" v-for="(item,index) of manufacturer" :key="index" v-text="item.manufacturerName"></option> </select>
                        </div>
                    </div>

                    <!-- 设备功能说明 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备功能说明</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('explain')&&addData.function_explain!='','has-error':errors.has('explain')}">
                            <textarea rows="6" class="form-control" name="explain" :placeholder="'请输入设备功能说明,最多为20位'" v-model="addData.function_explain" :data-vv-as="'设备功能说明'" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('explain')">{{errors.first("explain")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                    </div>
                </form>
            </div>
            <div v-show="equipmentDeployAdmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                  <!-- 部署场景节点 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">部署场景节点</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addDeployData.deploy_node_id_parent"><option :value="item.deployNodeId" v-for="(item,index) of deploy_type_parent" :key="index" v-text="item.nodeName"></option> </select>
                        </div>
                    </div>
                    <!-- 部署设备节点 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">部署设备节点</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addDeployData.deploy_node_id" @focus="deviceNodeFocus"><option :value="item.deployNodeId" v-for="(item,index) of deploy_type" :key="index" v-text="item.nodeName"></option> </select>
                        </div>
                    </div>
                    <!-- 设备功能说明 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">部署备注</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('remarks')&&addDeployData.remarks!='','has-error':errors.has('remarks')}">
                            <textarea rows="6" class="form-control" name="remarks" :placeholder="'请输入部署备注'" v-model="addDeployData.remarks" :data-vv-as="'部署备注'" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('remarks')">{{errors.first("remarks")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDeployDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="equipmentDeployClick(false)">返回</button>
                    </div>
                </form>
            </div>
            <tables v-show="!inforadmin&&!equipmentDeployAdmin&&!loginequipmentDeployAdmin" :tableSelectData="tableSelectData"></tables>
            <div v-show="loginequipmentDeployAdmin&&!equipmentDeployAdmin">
                <div class="buttonbox">
                    <button type="button" class="btn btn-warning" @click="updateDeployData">修改部署信息</button>
                    <button type="button" class="btn btn-danger" @click="deleteData('deploy')">删除部署信息</button>
                    <button type="button" class="btn btn-warning" @click="closepanel">返回</button>
                </div>
               <form ref="resForm">
                    <table class="table table-bordered table-striped table-hover">
                    <thead>
                        <tr>
                            <th><input type="checkbox" name="" id=""></th>
                            <th v-for=" (title,index) in page.titleData" :key="index" v-text="title"></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(datas,index) in page.deployData" :key="index">
                           <td> <input type="checkbox"  @change="checkUpDeployData(datas,$event)"></td>
                            <td v-for=" (data,index) in datas" :key="index">
                                <span v-if="index=='deploy_time'" v-html="$api.opreate.datafilter(data,'yyyyMMdd')"></span>
                                <span v-else v-html="data"></span>
                            </td>
                        </tr>
                    </tbody>
                </table>
               </form>
            </div>
        </div>
    </div>
</div>
</template>

设备信息
<script>
import tables from "@/components/table.vue";
export default {
  data: function() {
    return {
      inforadmin: false,
      opreate: "add",
      tableSelectData: {
        selectTableurl: "equipment/selectEquipmentInfo.action",
        pageloginData: {
          id: this.stateid,
          state: {
            statetype_id: 0, //代表状态类型 0表示部署和全部，1表示审核 2表示运行状态
            statetype: "00", //代表在状态类型下的格式
            statetype_style_id: 0 //提交到后台的状态id
          }
        },
        filterName: []
      },
      addData: {
        equipment_type_id: "", //设备类型ID
        equipment_name: "", //设备名称
        control_type_id: "", //控制类型ID
        manufacturer_id: "", //厂商ID
        function_explain: "" //功能说明
      },
      addDeployData: {
        remarks: "", //备注
        deploy_node_id_parent: "", //部署场景节点id
        deploy_node_id: "", //部署设备节点id
        equipment_id: "" //设备id
      },
      equipmentDeployAdmin: false,
      equipment_type: [], //设备类型
      control_type: [], //设备控制类型
      manufacturer: [], //设备厂商
      deploy_type_parent:[],//部署场景节点
      deploy_type: [], //部署设备节点
      loginequipmentDeployAdmin: false, //查看部署节点详情
      page: {
        deployData: [],
        titleData: [],
        checkDeployData: []
      },
    };
  },
  props: {
    stateid: String,
    statetitle: String
  },
  computed: {
    checkData() {
      return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
    }
  },
  components: {
    tables
  },
  methods: {
    //调用添加和修改设备信息时获取信息的函数
    loginAddequipInfor() {
      this.loginEquipment_type();
      this.loginControl_type();
      this.loginManufacturer();
    },
    //添加函数
    addDatas() {
      var vm = this;
      var stateId = vm.stateid;
      var data = vm.addData;
      var datas = {
        data: data,
        states: {
          id: stateId
        }
      };
      // 修改操作
      var bool;
      if (vm.opreate == "update") {
        bool = vm.$api.http.opreateHttp(vm, "equipment/updateEquipmentInfo.action", datas, "修改设备信息", "");
      } else if (vm.opreate == "add") {
        //添加操作
        bool = vm.$api.http.opreateHttp(
          vm,
          "equipment/addEquipmentInfo.action",
          datas,
          "添加设备信息",
          ""
        );
      }
      bool.then(function(success) {
            vm.inforAddClick(false);
      });
    },
    //添加部署节点函数
    addDeployDatas() {
      var vm = this;
      var stateId = vm.stateid;
      var data = vm.addDeployData;
      var datas = {
        data: data,
        states: {
          id: stateId
        }
      };
      var bool;
      if(vm.page.checkDeployData.length>0&&vm.loginequipmentDeployAdmin){
          bool = vm.$api.http.opreateHttp(vm, "equipment/updateDeployDetails.action", datas, "修改部署信息", "");
      }else{
          bool = vm.$api.http.opreateHttp(vm, "equipment/deployEquipment.action", datas, "部署设备", "");
      }
      setTimeout(function() {
        vm.$validator.errors.clear();
      }, 300);
      bool.then(function(success) {
        if (success) {
          setTimeout(function() {
            vm.equipmentDeployClick(false);
          }, 800);
        }
      });
    },
    /**按钮按下操作
     *
     *
     */
    //详情信息选框选中
    checkUpDeployData(data, e) {
      var vm = this;
      var check = e.target.checked;
      var checkData = vm.page.checkDeployData;
      var index = checkData.indexOf(data);
      if (check) {
        checkData.push(data);
      } else {
        checkData.splice(index, 1);
      }
    },
    //部署节点按钮按下
    equipmentDeployClick(bool) {
      var vm = this;
      if (bool) {
        if (vm.$api.opreate.TESTcheck(vm, "部署节点")) {
          vm.addDeployData.equipment_id = vm.checkData[0].equipment_id;
          vm.loginDeploy_type_parent();
          vm.equipmentDeployAdmin = bool;
        }
      } else {
        vm.equipmentDeployAdmin = bool;
      }
    },
    //设备添加和返回按钮按下
    inforAddClick(bool) {
      var vm=this;
            var opreate=this.opreate;
            if(opreate=='update'){
                this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]);
            }
            async function Empty(){
                   for (var key in vm.addData) {
                    vm.addData[key] = "";
                    }
                    return true;
            }
          Empty().then(()=>{
               if (bool) {
        vm.loginAddequipInfor();
      }
            
                vm.opreate = "add";
                vm.inforadmin = bool;
                vm.$validator.errors.items=[]; //将表单错误数组重置为0
          })
    },
    // 修改设备信息按钮按下
    updateData() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm, "修改")) {
        vm.inforadmin = true;
        vm.opreate = "update";
        vm.addData = vm.$api.opreate.ObjectCopy(vm.checkData[0]);
        vm.loginAddequipInfor();
      }
    },
    // 修改设备部署信息按钮按下
    updateDeployData() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm, "修改",vm.page.checkDeployData)) {
        vm.equipmentDeployAdmin = true;
        vm.addDeployData = vm.page.checkDeployData[0];
      }
    },
    // 删除按钮按下
    deleteData(title) {
      var vm = this;
      var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
      if (title=='deploy'?vm.$api.opreate.TESTcheck(vm, "删除",vm.page.checkDeployData):vm.$api.opreate.TESTcheck(vm, "删除")) {
        //询问框
        vm.$layer.confirm(
          "是否删除",
          {
            btn: ["确认", "取消"] //按钮
          },
          function() {
            if (title=='deploy') {
                console.log( vm.page.checkDeployData)
              var datas = {
                id: vm.page.checkDeployData["0"].equipment_deploy_id,
                states: {
                  id: vm.stateid
                }
              };
              vm.$api.http.opreateHttp(vm, "equipment/deleteDeployDetails.action", datas, "删除");
            } else {

                var datas = {
                id: checkData[0].equipment_id,
                states: {
                  id: vm.stateid
                }
              };
              vm.$api.http.opreateHttp(vm, "equipment/deleteEquipmentInfo.action", datas, "删除设备信息");
            }
          }
        );
      }
    },
    //搜索按钮按下
    searchBtnClick() {
      var vm = this;
      if (vm.searchData.data != "") {
      } else {
        vm.$layer.msg("请输入场景地址名后搜索", {
          icon: 0
        });
      }
    },
    //调换审核状态查看
    changeAudit() {
      var vm = this;
      var statetypes = vm.tableSelectData.pageloginData.state.statetype.split(
        ""
      );
      vm.tableSelectData.pageloginData.state.statetype_id = statetypes[0];
      vm.tableSelectData.pageloginData.state.statetype_style_id = statetypes[1];
      console.log(vm.tableSelectData.pageloginData);
      vm.$api.http.loginTableData(
        vm,
        "equipment/selectEquipmentInfo.action",
        vm.tableSelectData.pageloginData
      );
    },
    //审核点击函数
    AduitTrue() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm, "审核")) {
        vm.$api.http.opreateHttp(vm, "", datas, "审核", "");
      }
    },
    //查看部署详情
    loginEquipmentDeployInfor() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm, "查看部署详情")) {
        vm.loginequipmentDeployAdmin = true;
        vm.loginEquipmentDeployNode();
      }
    },
    //返回按钮按下
    closepanel() {
      this.loginequipmentDeployAdmin = false;
      this.$refs.resForm.reset();
      this.page.checkDeployData=[];
    },
    /**select框获取数据
     *
     *
     */
    //设备类型获取
    loginEquipment_type() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "equipment/selectAllEquipType.action")
        .then(function(data) {
          if (data.bool) {
            vm.equipment_type = data.data.list;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //获取设备控制类型信息
    loginControl_type() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "equipment/selectAllEquipControType.action")
        .then(function(data) {
          if (data.bool) {
            vm.control_type = data.data.list;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 获取设备厂商信息
    loginManufacturer() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "equipment/selectAllEquipCompany.action")
        .then(function(data) {
          if (data.bool) {
            vm.manufacturer = data.data.list;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 获取设备部署场景节点信息
    loginDeploy_type_parent(){
      var vm = this;
      vm.$axios
        .post(vm.HOST + "equipment/selectAllDeployNode.action")
        .then(function(data) {
          if (data.bool) {
            vm.deploy_type_parent = data.data.list;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 获取设备部署设备节点信息
    loginDeploy_type() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "equipment/selectAllDeployNode.action",{id:vm.addDeployData.deploy_node_id_parent})
        .then(function(data) {
          if (data.bool) {
            vm.deploy_type = data.data.list;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    /**获取设备部署详情信息
     *
     *
     */
    loginEquipmentDeployNode() {
      var vm = this;
      vm.$axios
        .post(vm.HOST + "equipment/selectDeployDetails.action", {
          id: vm.checkData[0].equipment_id
        })
        .then(function(success) {
          let data = success.data;
          if (success.bool) {
              vm.page.titleData=data.data_title;
            vm.page.deployData = data.lists;
          } else {
            vm.$layer.msg("详情信息获取失败", {
              icon: 5
            });
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //部署设备节点select获取焦点函数
    deviceNodeFocus(){
      var vm=this;
      if(vm.addDeployData.deploy_node_id_parent!=""){
        vm.loginDeploy_type();
      }else{
        vm.$layer.alert('请先选择场景节点', {
      icon: 1,
      time: 2000, //2s后自动关闭
    } )
      }
    }
  }
};
</script>

<style>
</style>



